'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';

interface NavigationProps {
  activeSection: string;
  theme: string;
  onThemeToggle: () => void;
}

const Navigation: React.FC<NavigationProps> = ({ activeSection, theme, onThemeToggle }) => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const pathname = usePathname();

  const navLinks = [
    { href: '/#home', label: 'Home', section: 'home' },
    { href: '/#about', label: 'About', section: 'about' },
    { href: '/#skills', label: 'Skills', section: 'skills' },
    { href: '/#experience', label: 'Experience', section: 'experience' },
    { href: '/projects', label: 'Projects', section: 'projects' },
    { href: '/#photography', label: 'Photography', section: 'photography' },
    { href: '/resume', label: 'Resume', section: 'resume' },
    { href: '/blog', label: 'Blog', section: 'blog' },
    { href: '/#certifications', label: 'Certifications', section: 'certifications' },
    { href: '/#contact', label: 'Contact', section: 'contact' },
  ];

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const handleNavClick = (href: string) => {
    setIsMobileMenuOpen(false);
    
    // Handle same-page sections with smooth scrolling
    if (href.startsWith('/#') && pathname === '/') {
      const sectionId = href.substring(2); // Remove '/#'
      const target = document.querySelector(`#${sectionId}`);
      if (target) {
        const offsetTop = target.getBoundingClientRect().top + window.pageYOffset - 70;
        window.scrollTo({
          top: offsetTop,
          behavior: 'smooth'
        });
      }
    }
    // For cross-page navigation, Next.js Link will handle it
  };

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  const closeMobileMenu = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      setIsMobileMenuOpen(false);
    }
  };

  useEffect(() => {
    // Add staggered animation for menu items
    if (isMobileMenuOpen) {
      const navItems = document.querySelectorAll('.nav-item');
      navItems.forEach((item, index) => {
        (item as HTMLElement).style.animation = `fadeInUp 0.3s ease-out ${index * 0.1}s both`;
      });
    }
  }, [isMobileMenuOpen]);

  return (
    <nav className={`navbar ${isScrolled ? 'scrolled' : ''}`} id="navbar">
      <div className="nav-container">
        <div className="nav-logo">
          <Link href="/">
            <span>SM</span>
          </Link>
        </div>
        
        <ul className={`nav-menu ${isMobileMenuOpen ? 'active' : ''}`} id="nav-menu" onClick={closeMobileMenu}>
          {navLinks.map((link) => {
            const isActive = (link.section === 'resume' && pathname === '/resume') ||
                           (link.section === 'blog' && (pathname === '/blog' || pathname?.startsWith('/blog/'))) ||
                           (pathname === '/' && activeSection === link.section);

            return (
              <li key={link.href} className="nav-item">
                <Link
                  href={link.href}
                  className={`nav-link ${isActive ? 'active' : ''}`}
                  onClick={() => handleNavClick(link.href)}
                >
                  {link.label}
                </Link>
              </li>
            );
          })}
        </ul>

        <div className="nav-right">
          <button 
            id="theme-toggle" 
            className="theme-toggle" 
            aria-label={theme === 'dark' ? 'Switch to light mode' : 'Switch to dark mode'}
            onClick={onThemeToggle}
          >
            <i className={`fas ${theme === 'dark' ? 'fa-sun' : 'fa-moon'}`} id="theme-icon"></i>
          </button>
          
          <div 
            className={`hamburger ${isMobileMenuOpen ? 'active' : ''}`} 
            id="hamburger"
            onClick={toggleMobileMenu}
          >
            <span className="bar"></span>
            <span className="bar"></span>
            <span className="bar"></span>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navigation;