'use client';

import { useEffect, useRef, useState } from 'react';
import Image from 'next/image';

const About: React.FC = () => {
  const [counters, setCounters] = useState({
    experience: 0,
    projects: 0,
    industries: 0
  });
  const [hasAnimated, setHasAnimated] = useState(false);
  const sectionRef = useRef<HTMLElement>(null);

  const stats = [
    { key: 'experience', target: 3, suffix: '+', label: 'Years Experience' },
    { key: 'projects', target: 10, suffix: '+', label: 'AI Projects Deployed' },
    { key: 'industries', target: 5, suffix: '', label: 'Industries Impacted' }
  ];

  const education = [
    {
      degree: 'Master of Science in Data Science',
      institution: 'Indiana University Bloomington, USA',
      period: 'Aug 2022 – May 2024',
      logo: '/photos/1631332394799.jpeg',
      logoWebp: '/photos/1631332394799.webp',
      url: 'https://www.indiana.edu/',
      bgColor: '#7d120d'
    },
    {
      degree: 'Bachelor of Technology in Information Technology',
      institution: 'Thakur College of Engineering and Technology, India',
      period: 'Aug 2018 – May 2022',
      logo: '/photos/download.jpeg',
      logoWebp: '/photos/download.webp',
      url: 'https://www.tcetmumbai.in/',
      bgColor: '#141355'
    }
  ];

  const animateCounters = () => {
    if (hasAnimated) return;
    
    setHasAnimated(true);
    
    stats.forEach(stat => {
      let current = 0;
      const increment = stat.target / 100;
      const timer = setInterval(() => {
        current += increment;
        if (current >= stat.target) {
          setCounters(prev => ({ ...prev, [stat.key]: stat.target }));
          clearInterval(timer);
        } else {
          setCounters(prev => ({ ...prev, [stat.key]: Math.round(current) }));
        }
      }, 20);
    });
  };

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting && !hasAnimated) {
          animateCounters();
        }
      },
      { threshold: 0.5 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => observer.disconnect();
  }, [hasAnimated]);

  return (
    <section id="about" className="about" ref={sectionRef}>
      <div className="container">
        <div className="section-header">
          <h2 className="section-title">About Me</h2>
          <div className="section-divider"></div>
        </div>
        <div className="about-content">
          <div className="about-text">
            <p className="about-intro">
              AI/ML Engineer with 3+ years of experience building scalable solutions using generative AI, LLMs, and NLP. 
              Skilled in designing agentic systems, document intelligence workflows, and ML pipelines deployed at scale.
            </p>
            <p>
              I specialize in driving automation and data-backed insights across industries like insurance, esports, 
              and enterprise analytics. My expertise spans from research and development to production deployment of 
              AI systems that make a real-world impact.
            </p>
            <div className="education">
              <h3>Education</h3>
              
              {education.map((edu, index) => (
                <div key={index} className="education-item">
                  <div className="education-logo">
                    <a 
                      href={edu.url} 
                      target="_blank" 
                      rel="noopener noreferrer" 
                      aria-label={edu.institution}
                    >
                      <picture>
                        <source srcSet={edu.logoWebp} type="image/webp" />
                        <img 
                          src={edu.logo}
                          alt={`${edu.institution} Logo`} 
                          height={40} 
                          style={{ 
                            backgroundColor: edu.bgColor, 
                            padding: '6px', 
                            borderRadius: '8px' 
                          }} 
                          loading="lazy" 
                          decoding="async"
                        />
                      </picture>
                    </a>
                  </div>
                  <div className="education-content">
                    <h4>{edu.degree}</h4>
                    <p>{edu.institution} | {edu.period}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
          <div className="about-stats">
            {stats.map((stat) => (
              <div key={stat.key} className="stat-item">
                <div className="stat-number">
                  {counters[stat.key as keyof typeof counters]}{stat.suffix}
                </div>
                <div className="stat-label">{stat.label}</div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;