'use client';

import { useState, useEffect } from 'react';

export const useScrollSpy = (sectionIds: string[], offset: number = 100) => {
  const [activeSection, setActiveSection] = useState<string>('');

  useEffect(() => {
    const updateActiveSection = () => {
      if (typeof window === 'undefined') return;
      
      const scrollPos = window.scrollY + offset;
      
      // Find the current section
      const sections = sectionIds.map(id => {
        const element = document.getElementById(id);
        if (!element) return null;
        
        return {
          id,
          offsetTop: element.offsetTop,
          offsetBottom: element.offsetTop + element.offsetHeight
        };
      }).filter(Boolean);

      // Find active section
      const currentSection = sections.find(section => {
        if (!section) return false;
        return scrollPos >= section.offsetTop && scrollPos < section.offsetBottom;
      });

      if (currentSection) {
        setActiveSection(currentSection.id);
      } else {
        // If we're at the very top, set to first section
        if (window.scrollY < 100 && sections.length > 0) {
          setActiveSection(sections[0]?.id || '');
        }
      }
    };

    // Throttle function for better performance
    let ticking = false;
    const throttledUpdate = () => {
      if (!ticking) {
        requestAnimationFrame(() => {
          updateActiveSection();
          ticking = false;
        });
        ticking = true;
      }
    };

    // Initial call
    updateActiveSection();
    
    // Add scroll listener
    window.addEventListener('scroll', throttledUpdate);
    
    return () => {
      window.removeEventListener('scroll', throttledUpdate);
    };
  }, [sectionIds, offset]);

  return activeSection;
};