'use client';

import { useRef, useEffect } from 'react';

const Skills: React.FC = () => {
  const skillsRef = useRef<HTMLElement>(null);

  const skillCategories = [
    {
      icon: 'fas fa-code',
      title: 'Programming Languages',
      skills: ['Python', 'SQL', 'R', 'JavaScript']
    },
    {
      icon: 'fas fa-brain',
      title: 'AI/ML Frameworks & Tools',
      skills: ['Scikit-learn', 'TensorFlow', 'PyTorch', 'FastAPI', 'MLflow', 'SpaCy']
    },
    {
      icon: 'fas fa-robot',
      title: 'Generative AI & LLMs',
      skills: ['GPT-4', 'LangChain', 'RAG', 'Agentic AI', 'Vector DBs (FAISS)']
    },
    {
      icon: 'fas fa-language',
      title: 'NLP & Document Intelligence',
      skills: ['NER', 'Text Classification', 'Summarization', 'Sentiment Analysis']
    },
    {
      icon: 'fas fa-database',
      title: 'Data Engineering & Storage',
      skills: ['Pandas', 'NumPy', 'PySpark', 'AWS (SQS, Step Functions)', 'Azure Data Lake', 'Azure SQL']
    },
    {
      icon: 'fas fa-chart-bar',
      title: 'Visualization & BI',
      skills: ['Tableau', 'Power BI', 'R Shiny']
    }
  ];

  useEffect(() => {
    const handleHover = (e: Event) => {
      const tag = e.target as HTMLElement;
      if (tag.classList.contains('skill-tag')) {
        tag.style.transform = 'translateY(-3px) scale(1.05)';
      }
    };

    const handleLeave = (e: Event) => {
      const tag = e.target as HTMLElement;
      if (tag.classList.contains('skill-tag')) {
        tag.style.transform = 'translateY(0) scale(1)';
      }
    };

    const skillTags = document.querySelectorAll('.skill-tag');
    skillTags.forEach(tag => {
      tag.addEventListener('mouseenter', handleHover);
      tag.addEventListener('mouseleave', handleLeave);
    });

    return () => {
      skillTags.forEach(tag => {
        tag.removeEventListener('mouseenter', handleHover);
        tag.removeEventListener('mouseleave', handleLeave);
      });
    };
  }, []);

  return (
    <section id="skills" className="skills" ref={skillsRef}>
      <div className="container">
        <div className="section-header">
          <h2 className="section-title">Technical Skills</h2>
          <div className="section-divider"></div>
        </div>
        <div className="skills-grid">
          {skillCategories.map((category, index) => (
            <div key={index} className="skill-category">
              <h3>
                <i className={category.icon}></i> {category.title}
              </h3>
              <div className="skill-tags">
                {category.skills.map((skill, skillIndex) => (
                  <span key={skillIndex} className="skill-tag">
                    {skill}
                  </span>
                ))}
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default Skills;