'use client';

import Link from 'next/link';
import Image from 'next/image';

interface BlogPost {
  title: string;
  excerpt: string;
  author: string;
  date: string;
  category: string;
  image: string;
  slug: string;
}

const Blog: React.FC = () => {
  const blogPosts: BlogPost[] = [
    {
      title: 'AI vs Human Brain',
      excerpt: 'A deep dive into the fundamental differences between artificial intelligence and human cognition. Exploring what AI really is, how it works, and the philosophical question of whether machines can truly think.',
      author: 'Sanket Muchhala',
      date: 'August 3, 2025',
      category: 'AI/Philosophy',
      image: 'data:image/svg+xml;base64,',
      slug: 'ai-vs-human-brain'
    }
  ];

  const handleAllBlogsClick = () => {
    window.open('/blogs', '_blank');
  };

  return (
    <section id="blog" className="blog">
      <div className="container">
        <div className="section-header">
          <h2 className="section-title">Latest Blog Posts</h2>
          <div className="section-divider"></div>
          <div className="section-actions">
            <button onClick={handleAllBlogsClick} className="btn btn-primary">
              <i className="fas fa-external-link-alt"></i>
              View All Blogs
            </button>
          </div>
        </div>
        <div className="blog-grid single-blog">
          {blogPosts.map((post, index) => (
            <article key={index} className="blog-card">
              <div className="blog-image">
                <img src={post.image} alt={post.title} />
              </div>
              <div className="blog-content">
                <div className="blog-meta">
                  <span className="blog-author">{post.author}</span>
                  <span className="blog-date">{post.date}</span>
                  <span className="blog-category">{post.category}</span>
                </div>
                <h3 className="blog-title">{post.title}</h3>
                <p className="blog-excerpt">{post.excerpt}</p>
                <Link href={`/blogs/${post.slug}`} className="blog-read-more">
                  Read More <i className="fas fa-arrow-right"></i>
                </Link>
              </div>
            </article>
          ))}
        </div>
      </div>
    </section>
  );
};

export default Blog;