'use client';

import { useState, useEffect } from 'react';
import Image from 'next/image';

interface Photo {
  src: string;
  webpSrc: string;
  alt: string;
  title?: string;
  category?: string;
}

const Photography: React.FC = () => {
  const [selectedImage, setSelectedImage] = useState<Photo | null>(null);

  const photos: Photo[] = [
    {
      src: '/photography/photos/sunset-mountains.jpg',
      webpSrc: '/photography/photos/optimized/sunset-mountains.webp',
      alt: 'Sunset over mountains',
      title: 'Sunset Mountains',
      category: 'nature'
    },
    {
      src: '/photography/photos/urban-architecture.jpg',
      webpSrc: '/photography/photos/optimized/urban-architecture.webp',
      alt: 'Urban architecture',
      title: 'Urban Architecture',
      category: 'architecture'
    },
    {
      src: '/photography/photos/nature-closeup.jpg',
      webpSrc: '/photography/photos/optimized/nature-closeup.webp',
      alt: 'Nature closeup',
      title: 'Nature Closeup',
      category: 'nature'
    },
    {
      src: '/photography/photos/autumn-colors.jpg',
      webpSrc: '/photography/photos/optimized/autumn-colors.webp',
      alt: 'Autumn colors',
      title: 'Autumn Colors',
      category: 'nature'
    },
    {
      src: '/photography/photos/IMG_20190619_160550_Original-min.jpg',
      webpSrc: '/photography/photos/optimized/IMG_20190619_160550_Original-min.webp',
      alt: 'Summer vibes',
      title: 'Summer Vibes',
      category: 'lifestyle'
    },
    {
      src: '/photography/photos/IMG_5166-min.JPG',
      webpSrc: '/photography/photos/optimized/IMG_5166-min.webp',
      alt: 'Street photography',
      title: 'Street Photography',
      category: 'street'
    },
    {
      src: '/photography/photos/IMG_6517-min.JPEG',
      webpSrc: '/photography/photos/optimized/IMG_6517-min.webp',
      alt: 'Minimalist composition',
      title: 'Minimalist Composition',
      category: 'minimal'
    }
  ];

  const openLightbox = (photo: Photo) => {
    setSelectedImage(photo);
    document.body.style.overflow = 'hidden';
  };

  const closeLightbox = () => {
    setSelectedImage(null);
    document.body.style.overflow = 'auto';
  };

  const handleLightboxClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      closeLightbox();
    }
  };

  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape') {
        closeLightbox();
      }
    };

    if (selectedImage) {
      document.addEventListener('keydown', handleKeyDown);
      return () => document.removeEventListener('keydown', handleKeyDown);
    }
  }, [selectedImage]);

  return (
    <>
      <section id="photography" className="photography">
        <div className="container">
          <div className="section-header">
            <h2 className="section-title">Through My Lens</h2>
            <div className="section-divider"></div>
            <p className="section-subtitle">
              Capturing moments, emotions, and the beauty of the world around us through the art of photography
            </p>
          </div>
          
          <div className="photo-collage">
            {photos.map((photo, index) => (
              <div 
                key={index} 
                className="collage-item"
                onClick={() => openLightbox(photo)}
                style={{ cursor: 'pointer' }}
              >
                <picture>
                  <source srcSet={photo.webpSrc} type="image/webp" />
                  <img
                    src={photo.src}
                    alt={photo.alt}
                    loading="lazy"
                    decoding="async"
                    srcSet={`${photo.src} 800w, ${photo.src} 400w`}
                    sizes="(max-width: 768px) 400px, 800px"
                  />
                </picture>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Lightbox Modal */}
      {selectedImage && (
        <div 
          id="lightbox" 
          className="lightbox" 
          style={{ display: 'block' }}
          onClick={handleLightboxClick}
        >
          <div className="lightbox-content">
            <span className="lightbox-close" onClick={closeLightbox}>
              &times;
            </span>
            <img 
              id="lightbox-img" 
              src={selectedImage.src} 
              alt={selectedImage.alt}
            />
            <div className="lightbox-info">
              <h3 id="lightbox-title">{selectedImage.title || 'Photography'}</h3>
              <p id="lightbox-category">
                {selectedImage.category ? 
                  selectedImage.category.charAt(0).toUpperCase() + selectedImage.category.slice(1) : 
                  'Art'
                }
              </p>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Photography;