'use client';

import { useEffect } from 'react';
import Image from 'next/image';

interface ContactMethod {
  icon: string;
  label: string;
  href: string;
}

interface SocialLink {
  name: string;
  url: string;
  icon?: string;
  customIcon?: string;
  ariaLabel: string;
}

const Contact: React.FC = () => {
  const contactMethods: ContactMethod[] = [
    {
      icon: 'fas fa-envelope',
      label: 'sanketmuchhala1@gmail.com',
      href: 'mailto:sanketmuchhala1@gmail.com'
    },
    {
      icon: 'fas fa-phone',
      label: '+1 812-778-4451',
      href: 'tel:+18127784451'
    }
  ];

  const socialLinks: SocialLink[] = [
    {
      name: 'LinkedIn',
      url: 'https://www.linkedin.com/in/sanketmuchhala/',
      icon: 'fab fa-linkedin-in',
      ariaLabel: 'LinkedIn'
    },
    {
      name: 'GitHub',
      url: 'https://github.com/sanketmuchhala',
      icon: 'fab fa-github',
      ariaLabel: 'GitHub'
    },
    {
      name: 'Twitter',
      url: 'https://twitter.com/sanketmuchhala',
      icon: 'fab fa-twitter',
      ariaLabel: 'Twitter'
    },
    {
      name: 'Instagram',
      url: 'https://www.instagram.com/sanketmuchhala/',
      icon: 'fab fa-instagram',
      ariaLabel: 'Instagram'
    },
    {
      name: 'LeetCode',
      url: 'https://leetcode.com/sanketmuchhala/',
      customIcon: 'https://cdn.jsdelivr.net/gh/devicons/devicon/icons/leetcode/leetcode-original.svg',
      ariaLabel: 'LeetCode'
    }
  ];

  useEffect(() => {
    const handleContactMethodClick = (e: Event) => {
      const method = e.target as HTMLElement;
      if (method.classList.contains('contact-method')) {
        method.style.transform = 'translateX(15px) scale(0.98)';
        setTimeout(() => {
          method.style.transform = 'translateX(10px) scale(1)';
        }, 150);
      }
    };

    const contactMethods = document.querySelectorAll('.contact-method');
    contactMethods.forEach(method => {
      method.addEventListener('click', handleContactMethodClick);
    });

    return () => {
      contactMethods.forEach(method => {
        method.removeEventListener('click', handleContactMethodClick);
      });
    };
  }, []);

  return (
    <section id="contact" className="contact">
      <div className="container">
        <div className="section-header">
          <h2 className="section-title">Get In Touch</h2>
          <div className="section-divider"></div>
        </div>
        <div className="contact-content">
          <div className="contact-info">
            <h3>Let&apos;s Connect</h3>
            <p>
              I&apos;m always interested in discussing new opportunities, innovative projects, 
              or just connecting with fellow AI/ML enthusiasts. Feel free to reach out!
            </p>
            <div className="contact-methods">
              {contactMethods.map((method, index) => (
                <a 
                  key={index}
                  href={method.href} 
                  className="contact-method"
                >
                  <i className={method.icon}></i>
                  <span>{method.label}</span>
                </a>
              ))}
            </div>
          </div>
          <div className="social-links">
            <h3>Socials</h3>
            <p className="social-description">
              Connect with me on social platforms to stay updated with my latest work and insights.
            </p>
            <div className="social-icons">
              {socialLinks.map((social, index) => (
                <a 
                  key={index}
                  href={social.url} 
                  className="icon" 
                  target="_blank" 
                  rel="noopener noreferrer" 
                  aria-label={social.ariaLabel}
                >
                  {social.icon ? (
                    <i className={social.icon}></i>
                  ) : (
                    <img 
                      src={social.customIcon} 
                      alt={social.name}
                      width={24}
                      height={24}
                    />
                  )}
                </a>
              ))}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Contact;